import { mapState } from 'vuex'
import { getComments, deleteComments } from '@/api/article'
export default {
    name: 'articleComment',
    props: {
        slug: {
            type: String
        }
    },
    data() {
        return {
            myComment: '',
            comments: []
        }
    },
    computed: {
        ...mapState(['user'])
    },
   created() {
       this.getList()
    },
    methods: {
        async getList() {
            const { data } = await getComments(this.slug)
            this.comments = data.comments
        },
        async delCommet(comment) {
            await deleteComments(this.slug, comment.id)
            this.getList()
        },
        async postComment() {
            if(!this.myComment) return
            // 添加评论
            await addComment({
                comment: {
                    body: this.myComment
                }
            }, this.slug)
            this.myComment = ''
            this.getList()
        }
    }
}
